        //         "Referer":"http://10.2.0.107/admin/",
        //         "User-Agent":"Mozilla/5.0 (Macintosh; Intel Mac OS X 10_13_6) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/103.0.0.0 Safari/537.36"
        //     },
        //     "request_body":"",
        //     "request_content_type":"",
        //     "response_status":200,
        //     "response_context_type":"application/json; charset=utf-8",
        //     "response_body":"{\"status\":200,\"message\":\"OK\",\"data\":[\"\"]}",
        //     "tag":null
        // }
        // ]
        // tableData.value = tableData.value.map((item => {
        //     try {
        //       item['request_parma'] = JSON.stringify(item.request_parma, null, 2)
        //       // eslint-disable-next-line no-empty
        //     } catch (e) {
        //     }
        //     try {
        //       item['request_body'] = JSON.stringify(JSON.parse(item.request_body), null, 2)
        //       // eslint-disable-next-line no-empty
        //     } catch (e) {
        //     }
        //     try {
        //       item['response_body'] = JSON.stringify(JSON.parse(item.response_body), null, 2)
        //       // eslint-disable-next-line no-empty
        //     } catch (e) {
        //     }
        //     return item
        //   }))
      })
    }

    const jsonParse = (json) => {
      if(!json)return ''
      //这里做判断  判断后端返回的类型
      if(JSON.stringify(json).includes('\n')){
        json = JSON.stringify(json, null, 2).split('\n')
      }else{
        //把字符类型转为带`\n`的字符json   然后再走下面的逻辑
        let obj = {}
        obj = eval(JSON.parse(json))
        json = JSON.stringify(obj,null,'\t')
        json = json.split('\n')
      }
      let dom = ''
      let indent = 0
      json.forEach( item => {
        if(item.indexOf('{')===0){
          dom += `<p style="text-indent:${indent}px">${item}</p>`
          indent += 16
        }
        else if(item.indexOf('{')===item.length-1){
          dom += `<p style="text-indent:${indent}px">${item}</p>`
          indent += 16
        }
        else if(item.indexOf('[')===item.length-1){
          dom += `<p style="text-indent:${indent}px">${item}</p>`
          indent += 16
        }
        else if(item.indexOf(': [')!==-1){
          dom += `<p style="text-indent:${indent}px">${item}</p>`
          indent += 16
        }
        else if(item.indexOf('}')!==-1){
          indent = indent - 16
          dom += `<p style="text-indent:${indent}px">${item}</p>`
        }

        else if(item.indexOf(']')===0){
          indent = indent - 16
          dom += `<p style="text-indent:${indent}px">${item}</p>`
        }
        else if(item.indexOf(']')===item.length-1){
          indent = indent - 16
          dom += `<p style="text-indent:${indent}px">${item}</p>`
        }
        else if(item.indexOf('],')===item.length-1){
          indent = indent - 16
          dom += `<p style="text-indent:${indent}px">${item}</p>`
        }
        else if(item.indexOf('],')===0){
          indent = indent - 16
          dom += `<p style="text-indent:${indent}px">${item}</p>`
        }
        else if(item.indexOf(']')===item.length-2){
          indent = indent - 16
          dom += `<p style="text-indent:${indent}px">${item}</p>`
        }
        else{
          dom += `<p style="text-indent:${indent}px">${item}</p>`
        }


      })
      return dom
    }

    const handleCurrentChange = (val) => {
      currentIndex.value = val
      getApiList()
    }

    const handleRefresh = () => {
      refresh(() => {
        ElMessage.success("刷新成功")
      })
    }

    // const  {proxy} = getCurrentInstance();
    // 复制
    const copy = (v, index) => {
      let clipboardObj = navigator.clipboard;
      let text = v;
      if(!v) {
        clipboardObj.writeText('')
        return;
      }
      clipboardObj.writeText(text)
      console.log(index)
      ElMessage.success("复制成功")
      // proxy.$nextTick(()=>{
      //   console.log(proxy.$refs['paramInfoRef_1'])
      //     proxy.$refs['paramInfoRef_1'].select();
      // })
    }
    return {
      tableData,
      total,
      colorsList,
      options,
      handleRefresh,
      jsonParse,
      copy,
      handleCurrentChange
    }
  }
}
</script>

